(function() {
  'use strict';

  angular
    .module('githubRanked')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('githubRanked')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($scope, $log, localStorageService, ORGANIZATION) {
      $scope.search = function(org) {
        $log.debug(ORGANIZATION.CHANGE_EVENT + " " + org);
        localStorageService.set("github-ranked-org",org);
        $scope.$emit(ORGANIZATION.CHANGE_EVENT,org);
        $scope.org = "";
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('githubRanked')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('githubRanked')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($scope, $log, $state, ORGANIZATION) {
    $scope.$on(ORGANIZATION.CHANGE_EVENT, function() {
      $state.go('organization', {}, {reload: true});
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('githubRanked')
    .service('OrganizationService', OrganizationService);

  /** @ngInject */
  function OrganizationService($http) {
      this.repos = function(id) {
        return $http({
          method: 'GET',
          url: 'http://api.github.com/orgs/' +id + '/repos'
        });
      }
  }
})();

(function() {
  'use strict';

  angular
    .module('githubRanked')
    .controller('OrganizationController', OrganizationController);

  /** @ngInject */
  function OrganizationController($log, $stateParams, toastr, localStorageService, OrganizationService, ORGANIZATION) {
    $log.debug("organization " + $stateParams.org);
    var vm = this;
    vm.org = localStorageService.get("github-ranked-org") || ORGANIZATION.DEFAULT;
    $log.debug(vm.org);
    OrganizationService.repos(vm.org).then(function(response) {
        vm.repos = response.data;
        vm.organization = vm.repos[0].owner;
    }, function() {
        toastr.error('Can not find your organization.', 'Not found!')
    });
  }
})();

(function() {
  'use strict';

  angular
    .module('githubRanked')
    .service('commitService', commitService);

  /** @ngInject */
  function commitService($http) {
      this.commits = function(owner, repo) {
         return $http({
           method: 'GET',
           url: 'http://api.github.com/repos/'+ owner + '/' + repo + '/commits'
         });
      }
  }
})();

(function() {
  'use strict';

  angular
    .module('githubRanked')
    .controller('CommitController', CommitController);

  /** @ngInject */
  function CommitController($stateParams, toastr, commitService) {
      var vm = this;
      vm.org = $stateParams.org
      vm.repo = $stateParams.repo
      commitService.commits($stateParams.org, $stateParams.repo).then(function(response) {
          vm.commits = response.data;
      }, function() {
          toastr.error('Can not find your repo.', 'Not found!')
      });
  }
})();

